import fetch from "../Services/fetch";
const UploadFile = ({props, callback}) => {
  // const randomId = Math.floor(Math.random() * Date.now());
  
  const handleChange = async (evt) => {
    
      const form = new FormData();
      const file = evt.target.files[0];
      form.append("file", file);
      // form.append("email", Auth.userInfo.email);
      
      fetch.post(`${process.env.REACT_APP_HOST}${props.path}`, form, 'file')
      .then(res=>{
         if(callback)
         callback({name: (file.name).split('.').slice(0, -1).join('.'), url: res.data});
      })
      .catch(er=>{
        console.log(er)
      })
      
  }
  return (<input type="file" name={props.name} onChange={handleChange} accept="image/*" id={`ID${props.id}`} className="d-none"/>);
}
 
export default UploadFile;