/* eslint-disable react-hooks/exhaustive-deps */
import { useRef, useState } from "react";
import { useEffect } from "react";
import Auth from "../../Auth";
import DataTable from "../../Components/DataTable";
import Modal from "../../Components/Modal";
import Thumbnail from "../../Components/Thumbnail";
import fetch from "../../Services/fetch";
import { teamRoles, CONSTANT } from "../../Services/String";
const $ = window.$;
const moment = window.moment;
const toastr = window.toastr;

const ContractorEval = () => {
    const tblRef = useRef();
    const [status, setStatus] = useState(1);
    const header = [
        {title: 'First Name', data: 'firstName'},
        {title: 'Middle Name', data: 'middleName'},
        {title: 'Last Name', data: 'lastName'},
        {title: 'Email', data: 'email'},
      ];
    
    const[selected, setSelected] = useState({
        email: '',
        firstName: '',
        lastName: '',
        middleName: '',
        address: '',
        contactNum: '',
        region: '',
        commercialRemarks: '',
        technicalRemarks: '',
        team:[],
        documents: {},
        inspectEval: '',
        finalEval: '',
        
    });
    useEffect(()=>{
        
        getContractors();
        $('#staticBackdrop').on('hidden.bs.modal',(e) => {
           setIsUpdate(false); 
        });
        
        //Date range picker
        $('#reservation').daterangepicker({
            startDate: moment(new Date()).subtract(30, 'days'),
            endDate  : moment(new Date()),
            opens: 'right'
        }, (start, end, label) => {
            getContractors({from: start.format('YYYY-MM-DD 00:00:00'), end: end.format('YYYY-MM-DD 22:59:59')});
        });

        $('#staticBackdrop').on("hide.bs.modal", modalClose);
        $('.credential-dropdown li a').on('click', function(e){
            e.stopPropagation();
        })

    }, [status]);


    const[isUpdate, setIsUpdate] = useState(false);
    const getSelected = (rowData) =>{
        setIsUpdate(true);
        setSelected(rowData);
        $('#staticBackdrop').modal('show');
    }
    
    const handleChange = (e, teamIndex, tabBelong, staticValue) => {
        e.preventDefault();
        const target = e.target;
        if(target.name === "status"){
            setStatus(()=>{
              return e.target.value
            });
            return;
        }   
        if(teamIndex > -1){
            const team = selected.team;
            if(tabBelong === 'remarks'){
                team[teamIndex]['remarks'] = target.value;
            }
            if(tabBelong === 'buttons'){
                team[teamIndex]['status'] = staticValue;
            }
            return setSelected({...selected, team});
        }
        return setSelected({...selected, [target.name]:  target.value});
    }

    const getContractors = (dateRange) =>{
        let showMsg = true;
        if(!dateRange ) {
            dateRange = {from: moment(new Date()).subtract(30, 'days').format('YYYY-MM-DD 00:00:00') , end: moment().format('YYYY-MM-DD 23:59:59')};
            showMsg = false;
        }
        fetch.post(`${process.env.REACT_APP_HOST}/api/contractor-evalation/${dateRange.from}/${dateRange.end}`,{
            status: status
        })
        .then(res=>{
            // console.log(res.data);
            tblRef.current.init(res.data);
            // showMsg && window.toastr.success(CONSTANT.SUCCESS);
        }).catch(er=>{
            showMsg && window.toastr.error(CONSTANT.ERRROR);
        });
    }

     // render the team form fields
     const renderTeamFields = (obj, teamIndex, personIndex, status = 1) => {
        switch(obj.type){
          
            default:
              return (
                <div className="form-group col-sm-12 col-lg-4 col-md-6" key={personIndex}>
                    <label htmlFor="exampleInputPassword1" style={{display: 'block'}}>
                        <div className="dropdown" >
                        <a href="#/" className="text-left cursor-pointer" data-toggle="dropdown">
                            <i className="fas fa-paperclip"></i> Attach credentials
                        </a>
                        <ul className="dropdown-menu credential-dropdown w-100 px-2">
                            { renderTeamCredential(obj.credentials) }
                        </ul>
                        </div>
                      <div>{teamRoles(obj.role)}</div>
                    </label>
                    <input type="text" 
                    defaultValue={obj.value} name={obj.role}
                    disabled={status > 1? true: false}
                    className="form-control" placeholder="" />
                </div>
              );
        }
  
      }
    const renderTeamCredential = (obj) => {
        let result = [];
        for (const key in obj) {
          result.push(<li key={key}><a href={obj[key]} target="_blank" rel="noreferrer">{key}</a></li>)
        }
        return result;

    }
    // render team form
    const renderTeam = () => {
        return selected.team.map((team, teamIndex) => {
        return (<div id={"team-container"+teamIndex} className="row col-12 legend-container" key={teamIndex}>
                    <div className="col-12 pb-1 mb-4 font-weight-bold legend-group">
                        {team.title}
                    </div>
                    <div className="col-lg-12 clearfix"></div>
                    {
                        team.person.map((person, personIndex)=>{
                        return renderTeamFields(person, teamIndex, personIndex, team.status);
                        })
                    }
                    <div className="clearfix"></div>
                    <div className={"form-group col-12 p-3 bg-warning"}>
                        <label htmlFor="exampleInputPassword1">Remarks</label>
                        <textarea value={team.remarks || ''} onChange={(e)=>handleChange(e, teamIndex, 'remarks')} name="" id="" cols="30" rows="4" className="form-control"></textarea>
                        <div className={team.status === 1 ? "col-12 text-right pb-0 pr-0 modal-footer": "d-none"}>
                        <button className="btn btn-danger" onClick={(e)=>handleChange(e, teamIndex, 'buttons', 0)}>Reject</button>
                            <button className="btn btn-primary" onClick={(e)=>handleChange(e, teamIndex, 'buttons', 2)}>Approve</button>
                        </div>
                    </div>
        </div>);
        });
      }
    const confirmEvaluation = (e, status) => {
        e.preventDefault();
        if(!status){
            // selected['status'] = 2;
        }
        if(selected.finalEval)
        selected['status'] = 2;

        selected['isUpdatedTeam'] = 2;
        
        // toastr.success('Success');
        fetch.post(`${process.env.REACT_APP_HOST}/api/contractor/${Auth.id}/evaluate`, selected )
        .then(res=>{
            tblRef.current.remove(selected);
            toastr.success(CONSTANT.SUCCESS);
            $('#staticBackdrop').modal('hide');
        }).catch(er=>{
            toastr.error(CONSTANT.ERRROR);
        });
    }
    const modalClose = () => {
        $('#custom-tabs-three-form-tab').tab('show');
    }
    return (<>

        {/* Date range */}
        <div className="row">
            <div className="col-md-4 col-sm-6 col-12">
              <div className="info-box bg-primary px-3 flex-column">
                
                    <div className="form-group">
                      <label>Select Category:</label>
                      <select className="form-control" value={status} onChange={handleChange} name="status">
                        <option value="1">For Approval</option>
                        <option value="3">Reviewed</option>
                        <option value="2">Confirmed</option>
                        
                      </select>
                    </div>
              </div>
            </div>
            <div className="col-md-4 col-sm-6 col-12">
                {/* <div className="info-box bg-primary px-3">
                    
                    <div className="form-group w-100">
                        <label>Date range:</label>

                        <div className="input-group">
                        <div className="input-group-prepend">
                            <span className="input-group-text">
                            <i className="far fa-calendar-alt"></i>
                            </span>
                        </div>
                        <input type="text" className="form-control float-right" id="reservation" />
                        </div>
                        
                    </div>
                    
                </div> */}
                {/* <!-- /.info-box --> */}
            </div>

            
        </div>

        {/* card */}
        <div className="card mt-3 card-outline card-primary">
            <div className="card-header">
            <h3 className="card-title">Partner Evaluation Table</h3>
            </div>
            {/* <!-- /.card-header --> */}
            <div className="card-body">
                <DataTable 
                getSelected={getSelected}
                header={header}
                ref={tblRef}
                buttons={true}
                />
            </div>
            {/* <!-- /.card-body --> */}
        </div>
        {/* <!-- /.card --> */}

        {/* <!-- Modal --> */}
        <Modal title="Partner Information" size="modal-lg"> 
            <div className="card-body row">
                    {/* <button onClick={testUpdate}>Test update</button> */}
                    <div className="card card-primary card-outline card-tabs w-100">
                        <div className="card-header p-0 pt-1 border-bottom-0">
                            <ul className="nav nav-tabs" id="custom-tabs-three-tab" role="tablist">
                            <li className="nav-item">
                                <a className="nav-link active" id="custom-tabs-three-form-tab" data-toggle="pill" href="#custom-tabs-three-form" role="tab" aria-controls="custom-tabs-three-form" aria-selected="false">Pre-assessment</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="custom-tabs-three-team-tab" data-toggle="pill" href="#custom-tabs-three-team" role="tab" aria-controls="custom-tabs-three-team" aria-selected="false">Team</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="custom-tabs-three-commercial-tab" data-toggle="pill" href="#custom-tabs-three-commercial" role="tab" aria-controls="custom-tabs-three-commercial" aria-selected="true">Commercial</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="custom-tabs-three-technical-tab" data-toggle="pill" href="#custom-tabs-three-technical" role="tab" aria-controls="custom-tabs-three-technical" aria-selected="true">Technical</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="custom-tabs-three-inspection-tab" data-toggle="pill" href="#custom-tabs-three-inspection" role="tab" aria-controls="custom-tabs-three-inspection" aria-selected="true">Inspection Evaluation</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" id="custom-tabs-three-finaleval-tab" data-toggle="pill" href="#custom-tabs-three-finaleval" role="tab" aria-controls="custom-tabs-three-finaleval" aria-selected="true">Final Evaluation</a>
                            </li>
                            </ul>
                        </div>
                        <div className="card-body">
                            <div className="tab-content" style={{overflow: "hidden"}}>
                            {/*  Pre assessment */}
                            <div className="tab-pane fade active show" id="custom-tabs-three-form" role="tabpanel" aria-labelledby="custom-tabs-three-form-tab">
                                    <div className="mt-3 row">
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputEmail1">Name of Contractor</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                defaultValue={selected.firstName}
                                                disabled
                                                name="contractorName"
                                                type="text" className="form-control" id="exampleInputEmail1" placeholder="Name" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Location</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                defaultValue={selected.address}
                                                disabled
                                                name="location"
                                                type="text" className="form-control" placeholder="Location" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Region</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                defaultValue={selected.region}
                                                disabled
                                                name="region"
                                                type="text" className="form-control" placeholder="Region" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Email</label>
                                                <input 
                                                defaultValue={selected.email}
                                                disabled
                                                type="text" className="form-control" placeholder="Email" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Contact Person</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                defaultValue={selected.firstName}
                                                disabled
                                                name="contactPerson"
                                                type="text" className="form-control" placeholder="Contact Person" />
                                            </div>
                                            <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                                <label htmlFor="exampleInputPassword1">Contact Number</label>
                                                <input 
                                                // onChange={handleChangeInfo}
                                                defaultValue={selected.contactNum}
                                                disabled
                                                name="contaactNum2"
                                                type="text" className="form-control" placeholder="Contact Number" />
                                            </div>  
                                    </div>
                            </div>
                            {/* team */}
                            <div className="tab-pane fade" id="custom-tabs-three-team" role="tabpanel" aria-labelledby="custom-tabs-three-profile-tab">
                                <div className="mt-3 row">
                                    {renderTeam()}
                                </div>
                            </div>
                            {/* commercial */}
                            <div className="tab-pane fade" id="custom-tabs-three-commercial" role="tabpanel" aria-labelledby="custom-tabs-three-commercial-tab">
                                <div className="mt-3 row">
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                                {
                                                disable: true,
                                                name: 'bznsPermit',
                                                title: 'Business Permit',
                                                src: selected.documents.bznsPermit,
                                                }
                                            }/>
                                        </div>
                                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                                {
                                                disable: true,
                                                name: 'certOfReg',
                                                title: 'Certificate of Registration',
                                                src: selected.documents.certOfReg,
                                                }
                                            }/>
                                        </div>
                                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                                {
                                                disable: true,
                                                name: 'DTISEC',
                                                title: 'DTI/SEC',
                                                src: selected.documents.DTISEC,
                                                }
                                            }/>
                                        </div>
                                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                                {
                                                disable: true,
                                                name: 'articleOfInc',
                                                title: 'Articles of Incorporation',
                                                src: selected.documents.articleOfInc,
                                                }
                                            }/>
                                        </div>
                                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                                {
                                                disable: true,
                                                name: 'AMO',
                                                title: 'AMO',
                                                src: selected.documents.AMO,
                                                }
                                            }/>
                                        </div>
                                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                                {
                                                disable: true,
                                                name: 'PCAB',
                                                title: 'PCAB Liscense',
                                                src: selected.documents.PCAB,
                                                }
                                            }/>
                                        </div>
                                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                                {
                                                disable: true,
                                                name: 'form2316',
                                                title: 'Form 2316',
                                                src: selected.documents.form2316,
                                                }
                                            }/>
                                        </div>
                                        <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                                {
                                                disable: true,
                                                name: 'auditFinStatement',
                                                title: 'Audited Financial Statements',
                                                src: selected.documents.auditFinStatement,
                                                }
                                            }/>
                                        </div>
                                        <div className="clearfix"></div>
                                        <div className="form-group col-12 p-3 bg-warning">
                                            <label htmlFor="exampleInputPassword1">Remarks</label>
                                            <textarea onChange={handleChange} value={selected.commercialRemarks || ''} name="commercialRemarks" cols="30" rows="4" className="form-control" ></textarea>
                                        </div>
                                </div>
                            </div>
                            {/* technical */}
                            <div className="tab-pane fade" id="custom-tabs-three-technical" role="tabpanel" aria-labelledby="custom-tabs-three-technical-tab">
                                <div className="mt-3 row">
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                            {
                                                name: 'qualityPlan',
                                                title: 'Quality Plan, ITP',
                                                src: selected.documents.qualityPlan,
                                            }
                                            }/>
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                            {
                                                name: 'safetyPlan',
                                                title: 'Safety Plan, JHA, RA, Risk Matrix',
                                                src: selected.documents.safetyPlan,
                                            }
                                            }/>
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                            {
                                                name: 'methodStatement',
                                                title: 'Method Statement',
                                                src: selected.documents.methodStatement,
                                            }
                                            }/>
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                            {
                                                name: 'stratPlan',
                                                title: 'Strategy Plan',
                                                src: selected.documents.stratPlan,
                                            }
                                            }/>
                                    </div>
                                    <div className="form-group col-sm-12 col-lg-4 col-md-6">
                                            <Thumbnail props = {
                                            {
                                                name: 'emerPrepResponse',
                                                title: 'Emergency Preparedness & Response',
                                                src: selected.documents.emerPrepResponse,
                                            }
                                            }/>
                                    </div>
                                    <div className="form-group col-12 p-3 bg-warning">
                                        <label htmlFor="exampleInputPassword1">Remarks</label>
                                        <textarea onChange={handleChange} value={selected.technicalRemarks || ''} name="technicalRemarks" cols="30" rows="4" className="form-control"></textarea>
                                    </div>
                                </div>
                            </div>
                            {/* inspection evaluation */}
                            <div className="tab-pane fade" id="custom-tabs-three-inspection" role="tabpanel" aria-labelledby="custom-tabs-three-technical-tab">
                                <div className="mt-3 row">
                                        <Thumbnail props = {
                                        {
                                            name:  'inspectEval',
                                            title: 'Inspection Evaluation',
                                            src:   selected.inspectEval,
                                            path:  `/api/upload/${selected._userId}/file`,
                                        }}
                                        callback= {(url)=>{
                                            setSelected({...selected, inspectEval: url})}}
                                        />
                                </div>
                            </div>
                            {/* final evaluation */}
                            <div className="tab-pane fade" id="custom-tabs-three-finaleval" role="tabpanel" aria-labelledby="custom-tabs-three-technical-tab">
                                <div className="mt-3 row">
                                        <Thumbnail props = {
                                        {
                                            name:  'finalEval',
                                            title: 'Final Evaluation',
                                            src:   selected.finalEval,
                                            path:  `/api/upload/${selected._userId}/file`,
                                        }}
                                        callback= {(url)=>{
                                            setSelected({...selected, finalEval: url})
                                        }}
                                        />
                                </div>
                            </div>
                            </div>
                        </div>
                        
                    </div>

            </div>
            <div className="modal-footer card-footer text-right">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                {isUpdate && 
                (<>
                {/* <button type="button" className="btn btn-primary" onClick={(e)=>confirmEvaluation(e, 'onhold')}>On-hold</button> */}
                <button type="button" className={status === 1 || status === '1' || status === 3 || status === '3' ? "btn btn-primary" : "d-none"} onClick={confirmEvaluation}>Proceed</button></>)}
                
            </div>
        </Modal> 
                
    </>);
}
 
export default ContractorEval;