/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable no-restricted-globals */
import { useRef, useState} from "react";
import { useEffect } from "react";
import {times} from "lodash";
import Auth from "../../Auth";
import DataTable from "../../Components/DataTable";
import Modal from "../../Components/Modal";
import fetch from "../../Services/fetch";
import { CONSTANT } from "../../Services/String";
const $ = window.$;
const moment = window.moment;

const ApproveSite = () => {
    const tblRef = useRef();
    const [form, setForm] = useState({
      region: '',
      partner: '',
      team: '',
      selected: {
        team: [],
      }
    });
    const [selectedRow, setSelectedRow] = useState({});
    const [partners, setPartners] = useState([]);
    
    const [status, setStatus] = useState(1);
    const [dateRange, setDateRange] = useState({from: moment(new Date()).format('YYYY-MM-DD 00:00:00') , end: moment(new Date()).format('YYYY-MM-DD 23:59:59')});
    const header = [
        {title: 'Address', data: 'address'},
        {title: 'Contact Person', data: 'siteContactPerson'},
        {title: 'Contact Num', data: 'siteContactNum'},
        {title: 'Coordinates', data: 'location'},
        {title: 'Coordinator', data: '_idcontractor'},
        {title: 'Legal Assess Percentage', data: 'legalAssessPercent',
          render: function(data) {
            return data+'%'
          }
        },
        {title: 'Site Files', data: '_saqfileurl',  visible: true,
        render: function ( data, type, row, meta ) {
            return `<a onclick="event.stopPropagation();" href=${data && data !== 'undefined' ? data : process.env.REACT_APP_CDRIVE} target="_blank">Check File</a>`;
          }
        },
        {title: 'Contracts', data: 'contracts',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'LCR', data: 'LCR',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Tower Construction Permit', data: 'towerConsPermit',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Fencing Permit', data: 'fencingPermit',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Excavation Permit', data: 'excavationPermit',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Bldg Permit', data: 'bldgPermit',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Cutting Permit', data: 'cenroPermit',  visible: false,
          render: function ( data, type, row, meta ) {
            return data? `<i class="fas fa-check text-success"></i>` : `<i class="fas fa-times text-danger"></i>`;
          }
        },
        {title: 'Assess By', data: '_legalAssessBy',  visible: false,},
        {title: 'Date Assess', data: 'updated_at',  visible: false,
          render: function(data) {
            return moment(data).format('YYYY-MM-DD');
          }
        },
        {title: 'Remarks', orderable: false, data: 'actionRemarks' },
        {title: 'Action By', visible: false, data: '_actionby'},
        {title: 'Action Date', visible: false, data: 'action_date',
        render: function(data) {
          return moment(data).format('YYYY-MM-DD')
        }},
        {title: 'Partner', orderable: false, data: 'actionRemarks' },
        {title: 'Team', orderable: false, data: 'actionRemarks' },
        {title: 'Status', orderable: false, data: 'actionRemarks' },
      ];
    
    
    useEffect(()=>{
        
        getSiteSummary();
        $('#staticBackdrop').on('hidden.bs.modal',(e) => {
        //    setIsUpdate(false); 
        });
        
        //Date range picker
        $('#reservation').daterangepicker({
            opens: 'right'
        }, (start, end, label) => {
            setDateRange({from: start.format('YYYY-MM-DD 00:00:00'), end: end.format('YYYY-MM-DD 23:59:59')});
            getSiteSummary({from: start.format('YYYY-MM-DD 00:00:00'), end: end.format('YYYY-MM-DD 23:59:59')});

        });

    }, [status]);

    useEffect(()=>{
      $('#staticBackdrop').on("hide.bs.modal", modalClose);
    },[]);

    // const[isUpdate, setIsUpdate] = useState(false);
    
    
    const getSiteSummary = (date, selectedStat) =>{
      
        let showMsg = true;
        if(!date ) {
            date = dateRange;
            showMsg = false;
        }
        fetch.post(`${process.env.REACT_APP_HOST}/api/site/gm/approved`,{
          status: selectedStat || status
        })
        .then(async res=>{
            // console.log(res.data);
            await tblRef.current.init(res.data);

            // approved action button
            $('.site-eval-proceed-btn').on('click', function() {
              const mapId = $(this).data('id');
               changeStatus(mapId, 2);
            });

            // reject action butotn
            $('.site-eval-reject-btn').on('click', function() {
               const mapId = $(this).data('id');
               confirmReject(mapId);
            });
            // showMsg && window.toastr.success(CONSTANT.SUCCESS);
        }).catch(er=>{
            showMsg && window.toastr.error(CONSTANT.ERRROR);
        });
    }

    const changeStatus = (id, stat) => {
      
      fetch.post(`${process.env.REACT_APP_HOST}/api/site/legal-assess/${id}/update`, {
        status: stat,
        _actionby: Auth._id,
        action_date: moment(new Date()).format('YYYY-MM-DD HH:mm:ss'),
      })
      .then(res => {
        console.log(res)
        tblRef.current.remove({location: id});
      })
      .catch(er=>{})
    }

    const handleChange = (e) =>{
        const target = e.target;
        
        switch(target.name) {
          case 'partner':
            const partner = partners.filter(data => data._userId === target.value);
            setForm({...form, 
              [target.name]: target.value, 
              team: '',
              selected: partner[0]
            });
            break;

          case 'status':
            setStatus(()=>{
              return target.value
            });
            break;
          default:
            setForm({...form, [target.name]: target.value});
        }
        // if(target.name === "status"){
        //   setStatus(()=>{
        //     return e.target.value
        //   });
        //   // getSiteSummary(dateRange, e.target.value);
        // }
        // else{
        //   setForm({...form, [target.name]: target.value});
        // }
        
    }

    const handleChangeRegion = (e) => {
      const target = e.target;
      setForm({...form, [target.name]: target.value, partner: '', selected: {
        team: [],
      }});
      fetch.post(`${process.env.REACT_APP_HOST}/api/contractor/search`,{
        isUpdatedTeam: target.value
      })
      .then(res => {
        setPartners(res.data);
      })
      .catch(er=>{
        window.toastr.error(CONSTANT.ERRROR);
      })
    }

    const confirmReject = (id) => {
      setForm({location: id});
      $("#staticBackdrop").modal('show');
    }

    // const reject = () => {
      
    //   fetch.post(`${process.env.REACT_APP_HOST}/api/site/legal-assess/${form.location}/update`, {
    //     actionRemarks: form.actionRemarks || '',
    //     _actionby: Auth._id,
    //     action_date: moment(new Date()).format('YYYY-MM-DD'),
    //     status: 0,
    //   })
    //   .then(res => {
    //     $("#staticBackdrop").modal('hide');
    //     tblRef.current.remove({location: form.location});
    //   })
    //   .catch(er=>{
    //     console.log(er)
    //   })
    // }
    
    const getSelected = (selectedRow) => {
      // console.log(selectedRow);
      setSelectedRow(selectedRow);
      $('#staticBackdrop').modal('show');
    }

    const proceedSite = async () => {
      
      try {
        await fetch.post(`${process.env.REACT_APP_HOST}/api/site/gm/set-team`,{
          _location: selectedRow.location,
          _partner: form.partner,
          teamName: form.team,
          _createdby: Auth._id
        });
      } catch (error) {
        return window.toastr.error(CONSTANT.ERRROR);
      }
      await tblRef.current.remove(selectedRow);
      
      $('#staticBackdrop').modal('hide');
    }
    
    const modalClose = () => {
      setForm({
        region: '',
        partner: '',
        team: '',
        selected: {
          team: [],
        }
      });
      setPartners([]);
    }
    
    return (<>
        {/* Date range */}
        <div className="row">
            {/* <div className="col-md-4 col-sm-6 col-12">
                <div className="info-box bg-primary px-3">
                    <div className="form-group w-100">
                        <label>Date range:</label>

                        <div className="input-group">
                        <div className="input-group-prepend">
                            <span className="input-group-text">
                            <i className="far fa-calendar-alt"></i>
                            </span>
                        </div>
                        <input type="text" className="form-control float-right" id="reservation" />
                        </div>
                    </div>
                </div>
            </div> */}

            <div className="col-md-4 col-sm-6 col-12">
              <div className="info-box bg-primary px-3 flex-column">
                
                    <div className="form-group">
                      <label>Select Category:</label>
                      <select className="form-control" value={status} onChange={handleChange} name="status">
                        <option value="1">For Team Assign</option>
                        <option value="2">Site On Progress</option>
                        <option value="0">Site Completed</option>
                      </select>
                    </div>
              </div>
            </div>
        </div>
        <div className="card-footer mb-3">
          <div className="row">
            <div className="col-sm-4 col-6">
              <div className="description-block border-right">
                <span>&nbsp;</span>
                <h5 className="description-header">
                  <span className="description-percentage text-info">
                    <i className="fas fa-caret-down"></i> 
                    17
                  </span>
                </h5>
                <span className="description-text">SITE FOR TEAM ASSIGNMENT</span>
              </div>
            </div>
            <div className="col-sm-4 col-6">
              <div className="description-block border-right">
                <span>&nbsp;</span>
                <h5 className="description-header">
                  <span className="description-percentage text-warning">
                    <i className="fas fa-caret-left"></i> 
                    100
                  </span>
                </h5>
                <span className="description-text">SITE ON PROGRESS</span>
              </div>
            </div>
            <div className="col-sm-4 col-6">
              <div className="description-block">
                <span>&nbsp;</span>
                <h5 className="description-header">
                  <span className="description-percentage text-success">
                    <i className="fas fa-caret-up"></i> 
                    20
                  </span>
                </h5>
                <span className="description-text">SITE COMPLETED</span>
              </div>
            </div>
          </div>
        </div>
        {/* card */}
        <div className="card mt-3 card-outline card-primary">
            <div className="card-header">
            <h3 className="card-title">Site Legal Summary Table</h3>
            </div>
            {/* <!-- /.card-header --> */}
            <div className="card-body">
                <DataTable 
                getSelected={getSelected}
                header={header}
                ref={tblRef}
                buttons={true}
                />
            </div>
            {/* <!-- /.card-body --> */}
        </div>
        {/* <!-- /.card --> */}


        {/* modal */}
        <Modal title="Confirm Delete" size="modal-md">
        <div className="card-body row">
          {/* <div className="col-lg-12">
              <div className="form-group">
                <label>Remarks</label>
                <textarea className="form-control" rows="3" placeholder="Type here ..." 
                name="actionRemarks" onChange={handleChange} value={form.actionRemarks}>
                </textarea>
              </div>
          </div> */}
          <div className="col-md-4">
              <div className="form-group">
                <label>Select Region:</label>
                <select className="form-control" value={form.region} onChange={handleChangeRegion} name="region">
                  <option value="" disabled>------</option>
                  {
                    times(17, (i) => (
                      <option value={i+1} key={i}>Region {i +1}</option>
                    ))
                  }
                </select>
              </div>
          </div>
          <div className="col-md-8">
              <div className="form-group">
                <label>Select Partner:</label>
                <select className="form-control" value={form.partner} onChange={handleChange} name="partner">
                  <option value="" disabled>------</option>
                  {partners.map((partner, i) => {
                    return (
                      <option value={partner._userId} key={partner._userId}>
                        {partner.firstName} {partner.lastName}
                      </option>)
                  })}
                </select>
              </div>
          </div>
          <div className="col-md-12">
              <div className="form-group">
                <label>Select Team:</label>
                <select className="form-control" value={form.team} onChange={handleChange} name="team">
                  <option value="" disabled>------</option>
                  {form.selected.team.map((team, i) => {
                    return (
                      <option value={team.title} key={i}>
                        {team.title}
                      </option>)
                  })}
                </select>
              </div>
          </div>
        </div>
        <div className="modal-footer card-footer text-right">
            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
            <button type="button" className="btn btn-primary" onClick={proceedSite} >Continue</button>
        </div>
        </Modal>
    </>);
}
 
export default ApproveSite;